.header {
  padding-top: 30px;
  &--position {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10;
  }
  &__container {
    align-items: center;
    justify-content: space-between;
  }
  .nav {
    align-items: center;
    &__list {
      align-items: center;
      gap: 40px;
    }
    &__link {
      color: var(--light-color);
      font-family: var(--font-family);
      font-size: var(--second-size);
      font-weight: 400;
      line-height: 160%;
      transition: color 0.3s ease-in-out;
      &:focus {
        outline: none ;
      }
      @include hover {
        color: var(--main-color) ;
      }
      &:active {
        color: var(--active-color) ;
      }
    }
  }
  &__tel {
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: 30px;
    font-weight: 700;
    line-height: 160%;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none ;
    }
    @include hover {
      color: var(--main-color) ;
    }
    &:active {
      color: var(--active-color) ;
    }
  }
  .nav__link.focus-visible {
    outline: 1px solid var(--main-color);
    outline-offset: 3px;
    }
  .header__tel.focus-visible {
    outline: 1px solid var(--main-color);
    outline-offset: 3px;
    }
}

