.red {
  min-height: 460px;
  position: relative;
  padding: 100px 0 90px 0;
  background-color: var(--main-color);
  &__container {
    justify-content: space-between;
    padding: 0 53px;
  }


  &__img {
    position: absolute;
    bottom: 0;
  }
  &__title {
    margin: 0;
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: 36px;
    font-weight: 600;
    line-height: 130%;
    margin-bottom: 20px;
  }
  &__descr {
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: var(--second-size);
    font-weight: 400;
    line-height: 130%;
  }
}
