.card {
  min-height: 886px;
  padding: 120px 0;
  &__title {
    margin: 0;
    margin-bottom: 60px;
    grid-column: 7 span;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: var(--mid-size);
    font-weight: 600;
    line-height: 160%;
  }
  &__list {

  }
  &__item {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    border: 1px solid rgba(20, 16, 36, 0.2);
    grid-column: 4 span;
    height: 460px;
    transition: background-color 0.3s , color 0.3s ease-in-out;
    &:hover {
      background-color: var(--black-color);
      .card__subtitle {
        color: var(--light-color);


      }
      .card__package {
        color: var(--light-color);
      }
      .card__descr {
        color: var(--light-color);
      }
    }
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 83px;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: 30px;
    font-weight: 600;
    line-height: 160%;
  }
  &__package {
    margin-bottom: 20px;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: 50px;
    font-weight: 600;
    line-height: 160%;
  }
  &__descr {
    margin-bottom: 52px;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 400;
    line-height: 160%;
    text-align: center;
  }
}
