.footer {
  padding: 40px 0;
  background-color: var(--black-color);
  min-height: 160px;
  color: var(--light-color);
  &__container {
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-family: var(--font-family);
    font-size: 30px;
    font-weight: 700;
    line-height: 160%;
    letter-spacing: 0%;
  }
  &__tel {
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: 30px;
    font-weight: 600;
    line-height: 160%;
    &:hover {
      color: var(--main-color);
      transition: color 0.3s ease-in-out;
    }
  }
  &__subtitle {
    margin: 0;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 160%;
  }
}
