.feed-form {
  width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
  input {
    padding: 19px 20px;
    width: 100%;
    height: 64px;
    background-color: var(--light-color);
    border-radius: 5px;
    border: none;
    color: rgba(40, 41, 68, 0.7);
    font-family: var(--font-family);
    font-size: var(--main-size);
    font-weight: 400;
    line-height: 26px;
  }
}
