.scale {
  padding-top: 120px;
  min-height: 614px;
  &__descr {
    grid-column: 9 span;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: 30px;
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0%;
    span {
      font-weight: 600;
    }
  }
  &__list {
    padding-top: 60px;
  }
  &__item {
    grid-column: 3 span;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  }
  &__num {
    color: rgb(213, 32, 39);
    font-family: var(--font-family);
    font-size: var(--big-size);
    font-weight: 600;
    line-height: 160%;
    letter-spacing: 0%;
  }
  &__text {
    color: rgba(20, 16, 36, 0.7);
    font-family: var(--font-family);
    font-size: var(--main-size);
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0%;
  }
}
