.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--light-color);
  font-family: var(--font-family);
  font-size: var(--main-size);
  font-weight: 400;
  line-height: 160%;
  letter-spacing: 0%;
  text-align: center;
  border-radius: 5px;
  background: var(--main-color);
  width: 260px;
  height: 79px;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
  &:focus {
    outline: none;
  }
  @include hover {
    border-color: none;
    background-color: var(--black-color);
    color: var(--main-color);
  }
  &:active {
    border-color: var(--main-color);
    background-color: var(--black-color);
  }


  &__big {
    width: 280px;
    &:focus {
      outline: none;
    }
    @include hover {
      border-color: none;
      background-color: var(--light-color);
      color: var(--black-color);
    }
    &:active {
      border: none;
      background-color: var(--main-color);
    }
  }


  &__white {
    width: 251px;
    height: 56px;
    box-shadow: 0px 2px 8px 0px rgba(20, 16, 36, 0.1);
    background: rgb(255, 255, 255);
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 160%;
    border: none;
    &:focus {
      outline: none;
    }
    @include hover {
      border-color: none;
      background-color: var(--black-color);
      color: var(--light-color);
    }
    &:active {
      border: none;
      background-color: var(--main-color);
    }
  }
  &__black {
    width: 500px;
    height: 64px;
    background-color: var(--black-color);
    color: var(--light-color);
    font-size: var(--main-size);
  }
}

.btn.focus-visible {
  border-color: var(--main-color);
}
.btn__white.focus-visible {
  border: 1px solid red;
  border-color: var(--main-color);
}
.btn__big.focus-visible {
  border: 1px solid red;
  border-color: var(--main-color);
}
