.pros {
  min-height: 886px;
  padding: 120px 0;
  padding-bottom: 18px;
  &__title {
    margin: 0;
    margin-bottom: 60px;
    grid-column: 8 span;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: var(--mid-size);
    font-weight: 600;
    line-height: 160%;
    letter-spacing: 0%;
  }

  &__container {
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--black-color) rgba(20, 16, 36, 0.08); ;
  }
  &__list {
    column-gap: 40px;
  }
  &__item {
      height: 650px;
    width: 360px;
    flex-shrink: 0
  }
  &__img {
    width: 360px;
    height: 500px;
    margin-bottom: 20px;
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 10px;
  }
  &__descr {
    margin-bottom: 20px;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: var(--main-size);
    font-weight: 400;
    line-height: 160%;
  }

}
