// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Jost", sans-serif;
  // --second-family: "NoeDisplay", sans-serif;
  --second2-family: "PlayfairDisplay", sans-serif;
  --content-width: 1160px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --offset: 40px;
  // colors
  --light-color: #fff;
  --main-color: rgb(213, 32, 39) ;
  --black-color: rgb(20, 16, 36) ;
  --active-color: rgb(0, 221, 173) ;
  //size
  --main-size: 18px ;
  --second-size: 20px ;
  --mid-size: 50px ;
  --big-size: 70px ;

}

