.school {
  position: relative;
  min-height: 1080px;
  padding-top: 344px;
  background-color: #141024;
  color: var(--light-color);
  &__content {
    grid-column: 6 span;
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: var(--big-size);
    font-weight: 600;
    line-height: 160%;
    letter-spacing: 0%;
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 50px;
    color: var(--light-color);
    font-family: var(--font-family);
    font-size: var(--second-size);
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0%;
  }

  &__bg {
    position: absolute;
    top: 160px;
    right: 10px;
    width: 783px;
    height: 783px;
  }
}
