.course {
  min-height: 485px;
  padding: 100px 0;
  background: rgb(249, 249, 249);
  &__container {
    flex-direction: column;
  }
  &__title {
    margin: 0;
    color: var(--black-color);
    font-family: var(--font-family);
    font-size: var(--mid-size);
    font-weight: 600;
    line-height: 160%;
    align-self: start;
  }
  &__btn {
    align-self: end;
    margin-bottom: 10px;
  }
  &__item {
    grid-column: 4 span;
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    color: var(--black-color) ;
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%;
    letter-spacing: 0%;
  }
  &__descr {
    color: var(--black-color) ;
    font-family: var(--font-family);
    font-size: var(--main-size);
    font-weight: 400;
    line-height: 160%;
    letter-spacing: 0%;
  }
}
